<template>
  <div>
    <vueLazyWaterfall :list="imgsArr"
                      :width="width"
                      class="waterfall-wrap"
                      @load="getData"
                      @image-load="imageLoad"
                      @image-error="imageLoad"
                      ref="waterfall"
                      @click="clickFn"></vueLazyWaterfall>
    <div style="height: 5000px; background-color: #00C075"></div>
  </div>

</template>

<script>
  import vueLazyWaterfall from '../src/vue-lazy-waterfall/index.vue'
  import axios from 'axios'

  export default {
    name: 'app',
    data() {
      return {
        imgsArr: [],
        group: 0, // 当前加载的加载图片的次数
        pullDownDistance: 0,
        width: 990
      }
    },
    components: {
      vueLazyWaterfall
    },
    methods: {
      getData() {
        axios.get('./static/mock/data.json?group=' + this.group) // 真实环境中，后端会根据参数group返回新的图片数组，这里我用一个静态json文件模拟
          .then(res => {
            this.group++
            if (this.group === 10) { // 模拟已经无新数据，显示 slot="waterfall-over"
              this.$refs.waterfall.end()
              return
            }
            this.imgsArr = this.imgsArr.concat(res.data)
          })
      },
      clickFn({value, index, $event}) {
        // console.log({value, index, $event})
      },
      imageLoad({$event}) {
        // console.log($event)
      },
      changeImgArr() {
        axios.get('./static/mock/data-change.json') // 真实环境中，后端会根据参数group返回新的图片数组，这里我用一个静态json文件模拟
          .then(res => {
            this.imgsArr = res.data
          })
      }
    },
    created() {
      this.getData()
    },
  }
</script>

<style lang="scss">
  * {
    margin: 0;
    padding: 0;
  }

  a {
    color: #000;
    text-decoration: none;
    &:active {
      color: #000;
    }
  }

  .waterfall-wrap {
    margin: 10px auto;
  }

</style>



