<template>
  <ul class="vue-lazy-waterfall-wrap"
      ref="listRef"
      :style="{width: `${width}px`, 'padding-top': `${maxHeight}px`}">
    <li class="vue-lazy-waterfall-item"
        v-for="(item, index) in list"
        :style="item._style"
        :ref="`$el_${index}`"
        @click="clickItemHandler(item, index, $event)">
      <img :src="item.src" alt="">
      {{item.info}}
    </li>
    <li class="loading-placeholder" ref="loadRef"></li>
  </ul>
</template>


<script>
  import isInViewport from '../helpers/isInViewport'
  export default {
    name: 'vue-lazy-waterfall',

    data() {
      return {
        maxHeight: 0,
        isFinished: false,
        beginIndex: 0,
        stylings: []
      }
    },

    props: {
      list: Array,

      colNum: {
        type: Number,
        default: 5
      },

      width: {
        type: Number,
        default() {
          return window.innerWidth
        }
      },

      itemWidth: {
        type: Number,
        default() {
          return parseInt(this.width / this.colNum)
        }
      },

      itemHeight: {
        type: Number,
        default: 200
      },

      load: Function,

      createLazyLoader: Function
    },

    watch: {
      list() {
        this.render()
      }
    },

    mounted() {
      this.render()
      this.$on('preloaded', this.preloadedHandler)

      this.createLazyloadCallback()
    },


    destroyed() {
      if (this.lazyScrollHandler) {
        window.removeEventListener('scroll', this.lazyScrollHandler)
        this.lazyScrollHandler = null
      }
    },

    methods: {
      render() {
        this.calcListStyle()
      },
      throttle(fn, delay) {
        var st = +new Date()
        return () => {
          var et = +new Date()
          if (et - st >= delay) {
            st = et
            fn()
          }
        }
      },
      refresh(item, index) {
        const colNum = this.colNum
        var prev = index - colNum
        if (prev >= 0) {
          var $prevItem = this.$refs[`$el_${prev}`][0]
          var $cur = this.$refs[`$el_${index}`][0]
          var top = parseInt($prevItem.style.top) + $prevItem.offsetHeight
          item._style.top = `${top}px`
          $cur.style.top = `${top}px`
          this.maxHeight = Math.max(this.maxHeight, top + $cur.offsetHeight)
        }
      },
      scrollHandler() {
        if (!this.isFinished && isInViewport(this.$refs.loadRef)) {
          this.$emit('load')
        }
      },
      createLazyloadCallback() {
        if (this.createLazyLoader) {
          this.lazyScrollHandler = this.createLazyLoader(this.$refs.loadRef)
        } else {
          this.lazyScrollHandler = this.throttle(this.scrollHandler, 50)
        }

        window.addEventListener('scroll', this.lazyScrollHandler.bind(this))
      },
      preloadedHandler(items) {
        const colNum = this.colNum

        for (let index = 0; index < items.length; ++index) {
          let item = items[index]
          if (item._index >= colNum) {
            this.refresh(item, item._index)
          }
        }
      },
      end() {
        this.isFinished = true
      },
      calcListStyle() {
        const self = this
        const beginIndex = self.beginIndex
        var list = self.list
        var len = list.length - beginIndex
        var lazyList = []

        const done = (item) => {
          return (ev) => {
            item.$event = ev
            self.$emit(`image-${ev.type}`, item)
            len -= 1
            if (0 === len) {
              self.$nextTick(() => {
                self.$emit('preloaded', lazyList)
              })
            }
          }
        }

        for (let index = beginIndex; index < list.length; index++) {
          let item = list[index]
          let img = new Image()

          item._index = index

          img.onload = done(item)
          img.onerror = done(item)

          img.src = item.src

          self.calcItemStyle(item, index)
          lazyList.push(item)
        }

        self.beginIndex = self.list.length
      },
      calcItemStyle(item, index) {
        const colNum = this.colNum
        var left = 0
        var top = 0

        left = index % colNum * this.itemWidth
        top = parseInt(index / colNum) * this.itemHeight

        item._style = {
          width: this.itemWidth + 'px',
          left: `${left}px`,
          top: `${top}px`
        }
      },
      clickItemHandler(value, index, $event) {
        this.$emit('click', {value, index, $event})
      }
    }
  }
</script>


<style lang="scss" scoped>
  .vue-lazy-waterfall-wrap {
    position: relative;
    .loading-placeholder {
      height: 1px;
      width: 100%;
      background-color: transparent;
      visibility: hidden;
    }
    .vue-lazy-waterfall-item {
      position: absolute;
      padding: 5px;
      box-sizing: border-box;
      border: 1px solid red;
      overflow: hidden;
      img {
        width: 100%;
      }
    }
  }
</style>



